import { questionData } from "./seed";
import { reactive, readonly } from "vue";

const state = reactive({
  questionData,
  nickname: "",
  usedQuestions: [],
  selectedAnswer: "",
  resultOfAnswer: true,
});

const getters = {
  getQuestion: () => {
    return state.questionData.find(
      (question) =>
        question.id === state.usedQuestions[state.usedQuestions.length - 1]
    );
  },
  getQuestionNumber: () => {
    return state.usedQuestions.length;
  },
};

const mutations = {
  setUserName: (name) => {
    state.nickname = name;
  },
  setUsedQuestion: (id) => {
    state.usedQuestions.push(id);
  },
  setSelectedAnswer: (index) => {
    state.selectedAnswer = index;
  },
  setresultOfAnswer: (boolean) => {
    state.resultOfAnswer = boolean;
  },
  resetQuiz: () => {
    state.usedQuestions = [];
    state.selectedAnswer = "";
    state.resultOfAnswer = true;
  },
};

export default {
  state: readonly(state),
  getters,
  mutations,
};
